import { useEffect } from 'react';
import { UseDebounce } from '../types';

const DEFAULT_DEBOUNCE_DELAY_MS = 3000;

export const useDebounce: UseDebounce = ({
    dependencies = [],
    callback,
    delay = DEFAULT_DEBOUNCE_DELAY_MS,
}) => {
  useEffect(() => {
    const handler = setTimeout(() => {
      callback();
    }, delay ?? DEFAULT_DEBOUNCE_DELAY_MS);

    return () => {
      clearTimeout(handler);
    };
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [...(dependencies ?? []), delay, callback]);
}
