import Toastify from "toastify-js";
import { AnyObject } from "../types/common";
import { TOAST_DURATION_MS } from "./constants";

export const trimValueMoreSpaces = (value: string): string =>
  value.replace(/\s+/g, " ");

export const filterSomeDataItems = <T extends AnyObject>(
  oldData: T[],
  newData: T[],
  filterKey: string = "id"
): T[] => {
  let oldFilteredSomeData = [...oldData];

  newData.forEach((item: T) => {
    const someOldItemIndex = oldFilteredSomeData.findIndex(
      (oldItem: T) =>
        oldItem?.[filterKey] !== undefined &&
        oldItem[filterKey] === item[filterKey]
    );
    if (someOldItemIndex > -1) {
      oldFilteredSomeData[someOldItemIndex] = item;
    }
  });

  const newFilteredData = oldFilteredSomeData?.length
    ? newData.filter(
        (item: T) =>
          item?.[filterKey] !== undefined &&
          !oldFilteredSomeData.some(
            (oldItem: T) =>
              oldItem?.[filterKey] !== undefined &&
              oldItem[filterKey] === item[filterKey]
          )
      )
    : newData;

  return [...oldFilteredSomeData, ...newFilteredData];
};

export const showTost: (props: {
  isError?: boolean;
  duration?: number;
  text: string;
}) => void = ({ isError = false, duration = TOAST_DURATION_MS, text }) => {
  try {
    Toastify({
      text,
      duration: duration,
      close: true,
      gravity: "bottom",
      position: "center",
      stopOnFocus: true,
      style: {
        width: "95vw",
        background: isError ? "#dc3545" : "#0d6efd",
      },
    }).showToast();
  } catch (e) {
    console.log(`Utils@@showTost@@@Error: ${e}`);
  }
};
