import { useMemo } from "react";
import { useFormik } from "formik";
//import { defaultNativeFilterTileSelectValue } from "../components/FilterFields/component";
import {
  NativeFiltersInputValues,
  UseNativeFiltersProps,
} from "../types";

export const nativeFilterCrefTagPrompt = "--cref";
export const nativeFiltersFormikFormVersionUploadImageSupportsValue = 6;

export const useNativeFilters: UseNativeFiltersProps = ({
  initFormikValues,
  onFormikFormSubmit,
  oldUserDataActiveFilters,
}) => {
  const defaultInitialValues: NativeFiltersInputValues = useMemo(
    () => {
      const data = {
        aspect: "",
        stylize: "",
        chaos: "",
        weird: "",
        //tile: defaultNativeFilterTileSelectValue,
        seed: "",
        no: "",
      };
      const {parameters, ...restData} = oldUserDataActiveFilters ?? {parameters: []}; 

      if (!restData || !Object.keys(restData)?.length) {
        return data;
      }
      return {
        ...data,
        ...restData,
      };
    },
    [oldUserDataActiveFilters]
  );

  const finalInitialValues = useMemo(
    () => ({
      ...defaultInitialValues,
      ...initFormikValues,
    }),
    [defaultInitialValues, initFormikValues]
  );

  const formikFormData = useFormik<NativeFiltersInputValues>({
    initialValues: finalInitialValues,
    enableReinitialize: true,
    onSubmit: (...props) => onFormikFormSubmit?.(...props),
  });
  
  return formikFormData;
};
