import {
  ApiFiltersUserData,
  ApiPopUpFiltersParametersData,
  GetFiltersUserData,
  UpdateFiltersUserData,
} from "./types/common";
import { TELEGRAM_BOT_USER_DATA_ENDPOINT_URL } from "./utils/constants";

export const getFiltersUserData: GetFiltersUserData = async (
  telegramBotInitData
) => {
  try {
    const userInitData = telegramBotInitData?.initData;
    const url = `${TELEGRAM_BOT_USER_DATA_ENDPOINT_URL}${
      !!userInitData ? `?${userInitData}` : ""
    }`;
    const response = await fetch(url, {
      method: "GET",
    });
    const responseData = (!!response?.json && (await response?.json?.())) ?? {};

    if (responseData === undefined) {
      return;
    }
    return responseData?.data;
  } catch (e) {
    console.log(`api@@getFiltersUserData: ${e}`);
  }
};

export const updateFiltersUserData: UpdateFiltersUserData = async ({
  telegramBotInitData,
  nativeFiltersFormInputValues,
  popupsFiltersData,
}) => {
  try {
    let data: ApiFiltersUserData | undefined = {
      parameters: [],
      // Set Native Filters Data
      ...nativeFiltersFormInputValues,
    };
    const url = `${TELEGRAM_BOT_USER_DATA_ENDPOINT_URL}${
      !!telegramBotInitData ? `?${telegramBotInitData}` : ""
    }`;

    // Set Pop-Ups Filters Data
    data.parameters = popupsFiltersData.reduce((result, filterData) => {
      let itemsData: ApiPopUpFiltersParametersData[] = [];

      if (!filterData.activeFiltersOptions.length) {
        return result;
      }
      itemsData = [
        ...itemsData,
        ...filterData.activeFiltersOptions.map((filterItemData) => ({
          key_group: filterData.name,
          key_parameter: filterItemData.promptName,
          level: +filterItemData.promptValue,
        })),
      ];

      return [...result, ...itemsData];
    }, [] as ApiPopUpFiltersParametersData[]);
    data = {
      ...data,
      ...nativeFiltersFormInputValues,
    };

    const response = await fetch(url, {
      method: "POST",
      body: JSON.stringify({ data }),
    });

    if (!response.ok) {
      console.log(`api@@updateFiltersUserData@@@Error: [response] => ${response}`);
    }
    console.log({
      response,
      data: JSON.stringify({ data }),
    });
    return response.ok;
  } catch (e) {
    console.log(`api@@updateFiltersUserData: ${e}`);
  }

  return false;
};
